html body {
    margin: 0;
    padding: 0;
    font-family: 'Nunito', sans-serif;
    color: #000002;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    width: 100%;
}

/*TODO: move to react spring*/

::-moz-selection {
    background: #ffe57f;
}

::selection {
    background: #ffe57f;
}

/*TODO: move to react spring*/

a.m {
    transition: all .2s ease-in-out;
    text-decoration: none;
    display: inline-block;
    font-weight: normal;
    padding: 2px 3px;
    box-shadow: inset 0 -3px 0 #007bff;
    color: black;
}

/*TODO: move to react spring*/

a:hover.m {
    box-shadow: inset 0 -40px 0 0 #007bff;
    color: #fff;
}

/*TODO: move to react spring*/

.small-link {
    transition: all .2s ease-in-out;
    text-decoration: none;
    display: inline-block;
    box-shadow: inset 0 0 0 black;
    color: black;
    font-weight: bold;
    word-wrap: break-word;
}

/*TODO: move to react spring*/

.small-link:hover {
    box-shadow: inset 0 -1.5px 0 0 black;
    color: black;
    text-decoration: none;
}

.nav-pills .nav-link:not(.active) {
    border: solid white;
    color: #0087fa;
    border-radius: 0;
    border-width: 0 0 0 3px;
    background-color: inherit;
    margin-left: 1px;
}

.nav-pills .nav-link.active {
    background-color: inherit;
    color: #0087fa;
    border: solid #0087fa;
    border-radius: 0;
    border-width: 0 0 0 3px;
    margin-left: 1px;
}

:root {
    --base-size: 50px
}

.xl {
    font-size: calc(50px * 1.0);
    font-size: calc(var(--base-size) * 1.0);
}

.l {
    font-size: calc(50px * 0.75);
    font-size: calc(var(--base-size) * 0.75);
}

.m {
    font-size: calc(50px * 0.55);
    font-size: calc(var(--base-size) * 0.55);
}

.s {
    font-size: calc(50px * 0.40);
    font-size: calc(var(--base-size) * 0.40);
}

.xs {
    font-size: calc(50px * 0.3);
    font-size: calc(var(--base-size) * 0.3);
}

